<template>
  <div class="star" :class="starType">
    <span v-for="(itemClass,index) in itemClasses" :class="itemClass" class="star-item" :key="index"></span>
  </div>
</template>
<script>
  const LENGTH = 5;
  const CLS_ON = 'on';
  const CLS_HALF = 'half';
  const CLS_OFF = 'off';

  export default {
    name: 'Star',
    props: {
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      starType() {
        return 'star-' + this.size;
      },
      itemClasses() {
        let result = [];
        // 将评分的小数位转化，只能是0或5
        let score = Math.floor(this.score * 2) / 2;
        // 判断是否有小数，也就是半星
        let hasDecimal = score % 1 !== 0;
        let integer = Math.floor(score);

        // 有多少个全星
        for (let i = 0; i < integer; i++) {
          result.push(CLS_ON);
        }
        // 若有半星，也只能有一个，也push
        if (hasDecimal) {
          result.push(CLS_HALF);
        }

        // 用off星补全5个
        while(result.length < LENGTH) {
          result.push(CLS_OFF);
        }

        return result;
      }
    }
  }
</script>
<style lang="stylus" scoped>
  @import '~@/assets/stylus/mixins.styl'


  .star
    font-size: 0
    .star-item
      display: inline-block
      background-repeat: no-repeat
    &.star-48
      .star-item
        width: 20px
        height: 20px
        margin-right: 22px
        background-size: 20px 20px
        &:last-child
          margin-right: 0
        &.on
          bg-image('star48_on')
        &.half
          bg-image('star48_half')
        &.off
          bg-image('star48_off')
    &.star-36
      .star-item
        width: 15px
        height: 15px
        margin-right: 6px
        background-size: 15px 15px
        &:last-child
          margin-right: 0
        &.on
          bg-image('star36_on')
        &.half
          bg-image('star36_half')
        &.off
          bg-image('star36_off')
    &.star-24
      .star-item
        width: 10px
        height: 10px
        margin-right: 3px
        background-size: 10px 10px
        &:last-child
          margin-right: 0
        &.on
          bg-image('star24_on')
        &.half
          bg-image('star24_half')
        &.off
          bg-image('star24_off')
    
      
</style>